import { ImgProps } from '@miq/componentjs';
import { StaffModel, StaffService } from '../service';

export interface SiteType {
  navbar?: { label: string; path: string }[];
  /** Site setting slug */
  settings?: string;
  name?: string;
  domain?: string;
}

export type SettingType = Record<string, any> & {
  slug?: string;
  is_live?: string;
  site?: SiteType;

  logo?: string;
  logo_data?: ImgProps;

  contact_number?: string;
  contact_number_title?: string;
  contact_number_display?: string;
  contact_email?: string;
  whatsapp_link?: string;
  whatsapp_link_title?: string;

  ga_tracking?: string;
  fb_pixel?: string;
  fb_app_id?: string;
  fb_app_secret?: string;

  ct_title?: string;
  ct_text?: string;
  ct_html?: string;
  ct_image?: string;
  ct_image_data?: ImgProps;
};

export class SettingService extends StaffService<SettingType> {
  updatePages(values: any) {
    return this.patch(`${this._path}pages/`, values);
  }

  updateSite(values: any) {
    return this.patch(`${this._path}site/`, values);
  }
}

export function Setting(data: SettingType = {}) {
  return StaffModel<SettingType, SettingService>(data, 'settings/', SettingService);
}

export type SettingInstance = ReturnType<typeof Setting>;

export type IndexType = {};

export function Index(data: IndexType = {}) {
  return StaffModel<IndexType>(data, 'index/');
}

export type IndexInstance = ReturnType<typeof Index>;

