import * as React from 'react';
import { useSearchParams } from 'react-router-dom';
import Staff from '..';
import { useRequest } from '@miq/hookjs';
import Form, { FormProps, useForm } from '@miq/formjs';
import { SharedDataCtx, SharedDataState } from '@miq/contextjs';
import { Response } from '@miq/utiljs';
import { Setting, SettingInstance, SettingType } from './models';
import { Icons } from '@miq/componentjs';
import { FormPhoneInput } from '@miq/formjs/src/Inputs';

const SitePrivacyUpdateSection = React.lazy(() => import('./PrivacyUpdateSection'));

//#region SettingModel

const contactFields = ['contact_number', 'contact_number_title', 'contact_number_display', 'contact_email'];
const whaFields = ['whatsapp_number', 'whatsapp_link', 'whatsapp_link_title'];
const analyticsFields = ['ga_tracking', 'fb_pixel'];
const fbAppFields = ['fb_app_id', 'fb_app_secret'];
const fields = [...contactFields, ...whaFields, ...analyticsFields, ...fbAppFields];

//#endregion SettingModel

//#region GeneralSettingUpdateView

const View: React.FC<{ children?: React.ReactNode; back?: string }> = ({ children, back }) => (
  <Staff.View
    title="Site General Settings"
    back={back}
    actions={
      <Staff.NavLink to="./?tab=privacy">
        <Icons.Gear />
      </Staff.NavLink>
    }
  >
    {children}
  </Staff.View>
);

export default function GeneralSettingUpdateView() {
  const [params] = useSearchParams();
  const { site } = React.useContext<SharedDataState>(SharedDataCtx);
  const settingSlug = site.settings;

  const form = useForm(mapDataToFormValues({}));
  const { res, updateData } = useRequest(() => Setting({ slug: settingSlug }).retrieve(), {
    refreshDeps: [settingSlug],
    onSuccess: ({ data }) => form.setValues(mapDataToFormValues(data)),
  });

  const r = new Response(res);
  if (!r.isSuccess) return null;

  const instance = Setting(r.data);
  const sharedProps = { settingSlug, instance, context: form, res, onUpdate: updateData, site };

  const tab = params.get('tab');

  switch (tab) {
    case 'privacy':
      return (
        <View back="./">
          <SitePrivacyUpdateSection {...sharedProps} />
        </View>
      );

    default:
      return (
        <View back="../">
          <div className="d-grid grid-md-5 gap-2">
            <Staff.Section title="Site Details" className="span-md-3">
              <SiteUpdateForm {...sharedProps} />
            </Staff.Section>
          </div>
          <div>
            <Staff.Section title="Contact" text="What's the best way to contact you?">
              <SettingForm {...sharedProps} fields={contactFields} />
            </Staff.Section>

            <Staff.Section title="Whatsapp" text="A whatsapp icon will be added to your site.">
              <SettingForm {...sharedProps} fields={whaFields} />
            </Staff.Section>

            <Staff.Section title="Analytics" text="Set up third-party analytics.">
              <SettingForm {...sharedProps} fields={analyticsFields} />
            </Staff.Section>

            <Staff.Section title="Facebook App">
              <SettingForm {...sharedProps} fields={fbAppFields} />
            </Staff.Section>

            <Staff.Section title="Social" text="Display Your social media accounts. Comin Soon ..." />
          </div>
        </View>
      );
  }
}

//#endregion GeneralSettingUpdateView

//#region SettingForms

type SettingFormProps = Omit<FormProps, 'children'> & {
  instance: SettingInstance;
  context: ReturnType<typeof useForm>;
  fields: string[];
  settingSlug: string;
  onUpdate?: (r: any) => void;
};

const SettingForm = ({ settingSlug, instance, fields, ...props }: SettingFormProps) => {
  const { context } = props;
  const { onUpdate, ...rest } = props;

  const handleSubmit = (e: any) => {
    e.preventDefault();

    const values: any = {};

    fields.forEach((field) => {
      values[field] = context.values[field];
    });

    return instance
      .update(values)
      .then(({ data }: any) => {
        onUpdate?.(data);
        // toast.success.({ message: 'Site updated.' });
      })
      .catch((err: any) => {
        context.handleError(err);
      });
  };

  return (
    <Form {...rest} onSubmit={handleSubmit}>
      {fields.map((name, i) => {
        return getFormField(name, context, i);
      })}
      <div className="mt-1">
        <Form.Submit value="Save" className="btn-md btn-primary-3 min-w-25" />
      </div>
    </Form>
  );
};

//#endregion SettingForms

const getFormField = (name: string, form: ReturnType<typeof useForm>, key?: number | string) => {
  switch (name) {
    // CONTACT FIELDS
    case 'contact_number':
      return (
        <Form.Field
          key={key}
          label="Phone number"
          text="Write your (business) contact number without country extension."
          error={form.errors.contact_number}
        >
          <FormPhoneInput name="contact_number" className="w-100 w-md-50" />
        </Form.Field>
      );
    case 'contact_number_title':
      return (
        <Form.Field
          key={key}
          label="Phone number link text"
          text="Describe your phone number link (SEO)."
          error={form.errors.contact_number_title}
        >
          <Form.Text name="contact_number_title" className="w-100 w-md-50" maxLength={99} />
        </Form.Field>
      );
    case 'contact_number_display':
      return (
        <Form.Field
          key={key}
          label="Phone number display"
          text="Write a call to action for your contact number."
          error={form.errors.contact_number_display}
        >
          <Form.Text
            // required={props.required}
            name="contact_number_display"
            placeholder="Call us now"
            maxLength={99}
            className="w-100 w-md-50"
          />
        </Form.Field>
      );
    case 'contact_email':
      return (
        <Form.Field
          key={key}
          label="Email"
          text="Write your (business) contact email."
          error={form.errors.contact_email}
        >
          <Form.Text name="contact_email" type="email" className="w-100 w-md-50" maxLength={99} />
        </Form.Field>
      );

    // WHATSAPP
    case 'whatsapp_number':
      return (
        <Form.Field
          key={key}
          label="Whatsapp number"
          text="Write your international whatsapp number.(229 ...)"
          error={form.errors.whatsapp_number}
        >
          <Form.Phone name="whatsapp_number" className="w-100 w-md-50" />
        </Form.Field>
      );
    case 'whatsapp_link':
      return (
        <Form.Field label="Whatsapp link" text="Write your whatsapp link." key={key}>
          <Form.Text name="whatsapp_link" type="url" maxLength={200} className="w-100 w-md-50" />
        </Form.Field>
      );
    case 'whatsapp_link_title':
      return (
        <Form.Field
          label="Whatsapp link text"
          text="Describe your whatsapp link."
          error={form.errors.whatsapp_link_title}
          key={key}
        >
          <Form.Text name="whatsapp_link_title" className="w-100 w-md-50" maxLength={99} />
        </Form.Field>
      );

    // ANALYTICS
    case 'ga_tracking':
      return (
        <Form.Field label="Google Analytics Tracking Number" key={key}>
          <Form.Text name="ga_tracking" className="w-100 w-md-50" placeholder="G-" />
        </Form.Field>
      );
    case 'fb_pixel':
      return (
        <Form.Field label="Facebook Pixel" error={form.errors.fb_pixel} key={key}>
          <Form.Text name="fb_pixel" className="w-100 w-md-50" />
        </Form.Field>
      );

    // FACEBOOK APP
    case 'fb_app_id':
      return (
        <Form.Field label="Facebook App ID" error={form.errors.fb_app_id} key={key}>
          <Form.Text name="fb_app_id" className="w-100 w-md-50" />
        </Form.Field>
      );
    case 'fb_app_secret':
      return (
        <Form.Field label="Facebook App Secret" error={form.errors.fb_app_secret} key={key}>
          <Form.Text name="fb_app_secret" className="w-100 w-md-50" />
        </Form.Field>
      );

    default:
      return null;
  }
};

//#region SiteUpdateForm

const SiteUpdateForm = (props: { instance: SettingInstance }) => {
  const ctx = React.useContext(SharedDataCtx);
  const { site } = ctx;
  const form = useForm({ name: site.name, domain: site.domain });

  const { instance } = props;

  const handleSubmit = (e: any) => {
    e.preventDefault();
    return instance
      .updateSite({ name: form.values.name, domain: form.values.domain })
      .then((res: any) => {
        ctx.updateSite(res.data.site);
        // toast.success({ message: 'Site updated.' });
      })
      .catch((err: any) => form.handleError(err));
  };

  const disabled = !Boolean(site.settings);

  return (
    <Form context={form} onSubmit={handleSubmit}>
      <Form.Field
        label="Site name"
        text="Give a name to your site. Keep it short and clean.(65 Characters max.)"
        error={form.errors.name}
      >
        <Form.Text
          required
          name="name"
          placeholder={'example'}
          maxLength={65}
          minLength={2}
          disabled={disabled}
          className="w-100 w-md-50"
        />
      </Form.Field>

      <Form.Field
        label="Site domain"
        text="Enter your domain name url. Include the http(s) part."
        error={form.errors.domain}
      >
        <Form.Text
          required
          type="url"
          name="domain"
          maxLength={99}
          placeholder={'http(s)://example.com'}
          minLength={3}
          disabled={disabled}
        />
      </Form.Field>

      <div className="">
        <Form.Submit value="Save" className="btn-md btn-primary-3 min-w-25" />
      </div>
    </Form>
  );
};

//#endregion SiteUpdateForm

//#region HELPERS

const mapDataToFormValues = (data: SettingType = {}) => {
  const values: SettingType = {};

  fields.forEach((field) => {
    values[field] = data[field] || '';
  });
  return values;
};

//#endregion HELPERS
